.modal_wrap{
    width: 100%;
    height: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 1200;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0,0,0,0.8);
    /* border:10px solid red; */

}
.modal{
    position: absolute;
    max-width:920px;
    /* height: auto; */
    /* border:5px solid blue; */
   
    z-index: 1200;
}
.modal .btn_close{
    position: absolute;
    right: 20px;
    top: 20px;
    cursor: pointer;
    color:#fff; 
    background: rgba(0,0,0,0.7);
    border-radius:100% ;
    text-align: center;
    line-height: 35px;
    z-index:1201
}
.modal_img{
    width: 100%;
    height: auto;position: relative;
}

.content{
    padding: 50px;
    /* border:  1px solid blue; */
   
}

h2{
    
    font-size: 50px;
    color: #a4e4ff;
}

